e evaluación debe conocer todos los puntos de contacto con el sistema. Necesitan comprender cómo trabaja cada persona, las herramientas que utilizan y cómo acceden a la red. Así es como los auditores obtienen una mejor comprensión de las políticas de ciberseguridad.")])]),_c('div',{staticClass:"col-lg-4 col-8 order-lg-2 order-1 mb-lg-0 mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img72.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center",attrs:{"titulo":"Revisar los estándares de cumplimiento"}},[_c('div',{staticClass:"col-lg-7 order-lg-1 order-2"},[_c('p',{staticClass:"mb-0"},[_vm._v("Antes de pasar por los movimientos de una auditoría de seguridad cibernética, se debe analizar los requisitos de cumplimiento. Estas reglas y regulaciones varían según el estado o país en el que se encuentre. Los auditores necesitan todos los detalles de cumplimiento. Si no se tienen actualizados, ofrecerán un tutorial para garantizar que el negocio se alinee con los requisitos de la industria que realice.")])]),_c('div',{staticClass:"col-lg-4 col-8 order-lg-2 order-1 mb-lg-0 mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img73.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center",attrs:{"titulo":"Mostrar la estructura de red"}},[_c('div',{staticClass:"col-lg-7 order-lg-1 order-2"},[_c('p',{staticClass:"mb-0"},[_vm._v("Cuando los dueños de negocios preguntan cuál es el objetivo principal de una auditoría de seguridad, todo se reduce a la divulgación completa de las brechas de seguridad en sus empresas. Los auditores necesitan una visión completa de la estructura de la red. Deben tener acceso al equipo de TI que apoya al equipo de auditoría en cualquier procedimiento para identificar vulnerabilidades. Una vez que encuentran puertas traseras o brechas en su infraestructura, pueden averiguar si está protegido contra ellos o no.")])]),_c('div',{staticClass:"col-lg-4 col-8 order-lg-2 order-1 mb-lg-0 mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img74.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center",attrs:{"titulo":"Comprender las vulnerabilidades del sistema"}},[_c('div',{staticClass:"col-lg-7 order-lg-1 order-2"},[_c('p',{staticClass:"mb-0"},[_vm._v("La mayoría de los propietarios de negocios desconocen los riesgos a los que están expuestos antes de realizar una auditoría de seguridad cibernética. Una auditoría es esencialmente una revelación. Puede ver todos los problemas con sus defensas (si los hay). Comprenderá los riesgos que enfrenta en línea y las leyes y regulaciones que se aplican al negocio. También ayuda a que los auditores sepan exactamente qué partes de la red necesitan protección.")])]),_c('div',{staticClass:"col-lg-4 col-8 order-lg-2 order-1 mb-lg-0 mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img75.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center",attrs:{"titulo":"Evaluar la gestión de riesgos cibernéticos"}},[_c('div',{staticClass:"col-lg-7 order-lg-1 order-2"},[_c('p',{staticClass:"mb-0"},[_vm._v("Una auditoría de seguridad cibernética ofrece una descripción completa de cada vulnerabilidad en el sistema y cómo los piratas informáticos pueden explotarlas. Esto ayuda a actualizar el plan de gestión de riesgos cibernéticos. Si las políticas de defensa actuales son ineficaces, es hora de una actualización. Se pueden instalar herramientas de análisis mejoradas e implementar una nueva estrategia DLP.")])]),_c('div',{staticClass:"col-lg-4 col-8 order-lg-2 order-1 mb-lg-0 mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img76.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center",attrs:{"titulo":"Priorizar respuestas"}},[_c('div',{staticClass:"col-lg-7 order-lg-1 order-2"},[_c('p',{staticClass:"mb-0"},[_vm._v("Una vez finalizada la auditoría de seguridad cibernética, se puede decidir qué hacer a continuación con un mejor sentido de prioridad. La auditoría identifica qué parte de la red está más expuesta y ofrece soluciones para resolver estos problemas. Al priorizar las amenazas más apremiantes, se asegurará de que los datos de la empresa estén seguros mientras evita la mayoría de los ataques cibernéticos.")])]),_c('div',{staticClass:"col-lg-4 col-8 order-lg-2 order-1 mb-lg-0 mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img77.png"),"alt":""}})])])])],1),_vm._m(57),_c('p',[_vm._v("Una lista de verificación de auditoría de seguridad cibernética incluye los requisitos básicos que deben evaluar los auditores. La mayoría de los elementos de la lista de verificación se adaptan a cada empresa según la industria y el tamaño del negocio. Sin embargo, en cada auditoría se incluye un conjunto básico de categorías. Estos son los elementos esenciales que debe solicitar independientemente de su nicho:")]),_vm._m(58),_vm._m(59),_c('p',{staticClass:"mb-5"},[_vm._v("Una vez que aprenda a auditar la seguridad cibernética, debe responder otra pregunta: ¿con qué frecuencia debe ejecutar estas auditorías en los sistemas? La respuesta es engañosa. Depende del tamaño de la empresa y presupuesto. Las grandes multinacionales realizan auditorías de seguridad cibernética mensualmente ya que manejan grandes centros de datos. Una empresa mediana requiere estas auditorías dos veces al año, dependiendo del volumen de las operaciones. Las pequeñas empresas solo necesitan una auditoría anual.")]),_vm._m(60),_c('p',[_vm._v("Como propietario de un negocio, se debe comprender los riesgos y amenazas en internet. La red no está exenta de actores maliciosos. Las auditorías de ciberseguridad están diseñadas para ayudar a comprender las vulnerabilidades del sistema. Las auditorías periódicas pueden ayudar a aumentar la seguridad de los datos mientras mejoran la reputación con los clientes y socios comerciales. Una auditoría de ciberseguridad adecuada se centra en los datos y las operaciones en curso. Destaca las partes débiles de la infraestructura y red. Las auditorías de seguridad ayudan a mejorar la seguridad con informes detallados que indican lo que se debe mejorar. Las auditorías analizan todos sus activos y garantizan que sus procesos de seguridad funcionen de manera eficiente con actualizaciones y rectificaciones recomendadas.")]),_vm._m(61),_c('separador'),_vm._m(62),_vm._m(63),_vm._m(64),_c('p',[_vm._v("El marco NICE está diseñado por NIST para proporcionar un vocabulario y definiciones comunes para varios trabajos y conjuntos de habilidades de ciberseguridad. Dentro del marco, se definen varios trabajos diferentes y se describen las tareas asociadas y los conjuntos de habilidades. Una tarea importante dentro de la ciberseguridad es recopilar y analizar datos. En esta sesión del tema sobre ciberseguridad y sus auditorías, se describen algunos de los roles de trabajo que realizan esta tarea dentro del dominio cibernético, los conocimientos y habilidades necesarios para hacerlo y cómo obtener este conjunto de conocimientos y habilidades.")]),_c('div',{staticClass:"BGIMG15 p-4 mb-5",attrs:{"data-aos":"zoom-out"}},[_c('SlyderA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-8 order-lg-1 order-2",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"h4"},[_vm._v("Creación de requerimientos de cobranza")]),_c('p',{staticClass:"mb-0"},[_vm._v("El primer paso en el proceso de recopilación de datos es identificar qué datos deben recopilarse. Lograr esto requiere la capacidad de identificar brechas en los datos recopilados actualmente, determinar qué datos deben recopilarse y saber dónde se pueden encontrar esos datos. Una habilidad importante para lograr este paso es conocer las posibles fuentes de datos. Por ejemplo, saber qué datos se pueden recopilar de puntos finales, redes, datos de código abierto, bases de datos, etc.")])]),_c('div',{staticClass:"col-lg-4 col-8 order-lg-2 order-1 mb-lg-0 mb-4",attrs:{"data-aos":"fade-left"}},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img80.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-8 order-lg-1 order-2",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"h4"},[_vm._v("Recopilación de datos")]),_c('p',{staticClass:"mb-0"},[_vm._v("Una vez que se ha definido el esfuerzo de recopilación, el siguiente paso es realizar la recopilación real de los datos. Esta etapa del proceso también requiere el conocimiento de las fuentes de datos, pero se centra en los métodos y herramientas necesarios para realizar la recopilación sin introducir artefactos.")])]),_c('div',{staticClass:"col-lg-4 col-8 order-lg-2 order-1 mb-lg-0 mb-4",attrs:{"data-aos":"fade-left"}},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img81.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-8 order-lg-1 order-2",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"h4"},[_vm._v("Preprocesamiento")]),_c('p',{staticClass:"mb-0"},[_vm._v("Los datos recopilados rara vez son perfectos. Antes de realizar cualquier análisis, a menudo es necesario realizar un preprocesamiento. Esto permite que el analista elimine cualquier error o artefacto obvio en los datos, identifique los vacíos de recopilación que deben llenarse y estandarice o transforme los datos en un formato utilizable. En esta etapa del proceso, un analista se beneficiaría de una experiencia en análisis de datos, ya que se necesitan herramientas y técnicas específicas para completarlo.")])]),_c('div',{staticClass:"col-lg-4 col-8 order-lg-2 order-1 mb-lg-0 mb-4",attrs:{"data-aos":"fade-left"}},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img82.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-8 order-lg-1 order-2",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"h4"},[_vm._v("Análisis")]),_c('p',{staticClass:"mb-0"},[_vm._v("La etapa de análisis del proceso es donde la experiencia en ciencia de datos es más valiosa. En esta etapa, el analista debe estar familiarizado con las herramientas y técnicas estadísticas y de extracción de datos necesarias para convertir los datos sin procesar en inteligencia utilizable que pueda probar o refutar las hipótesis del analista. El analista también debe tener habilidades de programación y secuencias de comandos para realizar esfuerzos de análisis a escala y de manera eficiente.")])]),_c('div',{staticClass:"col-lg-4 col-8 order-lg-2 order-1 mb-lg-0 mb-4",attrs:{"data-aos":"fade-left"}},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img83.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-8 order-lg-1 order-2",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"h4"},[_vm._v("Informes")]),_c('p',{staticClass:"mb-0"},[_vm._v("Una etapa final e importante en el proceso es la presentación de informes. En esta etapa, el analista desarrolla visualizaciones e informes que permiten a las partes interesadas comprender los esfuerzos de recopilación y análisis y los resultados y conclusiones extraídos del análisis.")])]),_c('div',{staticClass:"col-lg-4 col-8 order-lg-2 order-1 mb-lg-0 mb-4",attrs:{"data-aos":"fade-left"}},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img84.png"),"alt":""}})])])])],1),_vm._m(65),_c('p',{staticClass:"mb-5"},[_vm._v("Dentro del marco NICE, NIST define tres roles de trabajo diferentes que realizan la recopilación de información cibernética: analista de explotación, analista de red objetivo y operador cibernético. Si bien estos trabajos pueden realizar tareas muy diferentes, utilizan muchas de las mismas herramientas, técnicas y procedimientos en la etapa de recopilación de datos del trabajo.")]),_vm._m(66),_vm._m(67),_c('p',[_vm._v("Si bien estos son tres trabajos muy diferentes, funcionan de manera similar. Los tres necesitarán algunos conocimientos fundamentales, la capacidad de realizar la recopilación, el procesamiento de datos y la comprensión de las implicaciones legales de su función:")]),_vm._m(68),_vm._m(69),_vm._m(70),_vm._m(71),_vm._m(72),_vm._m(73),_c('p',[_vm._v("Para cada fase, se describe el propósito de esa fase y el papel de la gestión de TI:")]),_c('div',{staticClass:"row justify-content-center align-items-center mb-4"},[_c('div',{staticClass:"col-lg-8 order-lg-1 order-2",attrs:{"data-aos":"fade-right"}},[_c('LineaTiempoD',{staticClass:"color-primario"},[_c('div',{staticClass:"row",attrs:{"numero":"a","titulo":"Etapa de anuncio"}},[_c('p',[_vm._v("Esta es la etapa durante la cual los auditores anuncian su intención de auditar grupos, características, sistemas, etc. La mayoría de los departamentos de auditoría interna intentan planificar su trabajo por adelantado. Incluso pueden conocer su horario planificado con un año completo de anticipación. No pueden divulgar más detalles que los nombres y fechas preliminares de la auditoría, auditor líder o líder del equipo. Cuanto mayor sea la demora, más suave será el plan. Al igual que cualquier otro equipo de operaciones, las personas, el clima, la tecnología y otros factores comerciales retrasan los cronogramas de revisión.")]),_c('p',{staticClass:"mb-0"},[_vm._v("A veces las auditorías son cíclicas y se tienen que hacer trimestralmente, cada dos años, etc. Estas auditorías son más fáciles de planificar. Se conoce la carga de trabajo asociada y tendrá un plan de dotación de personal para cumplir con los recursos necesarios para la auditoría. Sin embargo, a veces los oyentes aparecen sin previo aviso. Esto suele suceder cuando las cosas no salen según lo planeado.")])]),_c('div',{staticClass:"row",attrs:{"numero":"b","titulo":"Carta de compromiso"}},[_c('p',[_vm._v("Los equipos de auditoría utilizan cartas de compromiso comercial para describir los objetivos específicos de la empresa. Tal vez quieran evaluar el cumplimiento de HIPAA o revisar la efectividad de sus controles de administración de acceso e identidad. Deben definir claramente la intención y el cronograma propuesto para cada fase del compromiso. También es una oportunidad para que el equipo de auditoría se presente a la empresa. Comprender el equipo de auditoría y el papel de cada uno ayudará a que todo el proyecto funcione sin problemas.")]),_c('p',[_vm._v("Además de establecer los objetivos de la auditoría, la carta de compromiso comercial describe el alcance de la auditoría. Esto implica determinar qué sistemas se pueden revisar y el período de exploración (rango de fechas para la revisión) y otros detalles.")]),_c('div',{staticClass:"BG02 p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("La información del alcance suele ser solo una estimación de los auditores, ya que es posible que no tengan el conocimiento específico del sistema, para saber si el alcance es demasiado grande o pequeño para lograr los objetivos establecidos.")])])]),_c('div',{staticClass:"row",attrs:{"numero":"c","titulo":"Actividad de alcance y brechas auto identificadas"}},[_c('p',[_vm._v("Durante el ejercicio de alcance, el auditor le dirá qué sistemas planea examinar, el período de análisis, la cantidad de muestras que planea tomar, el tamaño de la muestra, etc. Parte de esta información se basará en la experiencia previa con la organización o sistema. Si un grupo interno lleva varios años realizando la misma auditoría, el alcance inicial puede ser correcto. Sin embargo, si esta es la primera vez que se realiza una auditoría, si la composición del equipo ha cambiado o si el sistema se ha sometido a una revisión, es posible que este no sea el caso.")]),_c('div',{staticClass:"BG02 p-4 mb-4"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-10"},[_c('p',{staticClass:"mb-0"},[_vm._v("Su fase de anuncio debe incluir una autoevaluación y un análisis de brechas. La mayoría de los auditores le permitirán proporcionarles información sobre defectos conocidos por adelantado. Si tiene un plan de acción y ha completado un proceso de planificación importante, puede ayudar a mejorar su puntaje de auditoría.")])]),_c('div',{staticClass:"col-lg-2 col-4"},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img94.svg"),"alt":""}})])])]),_c('p',{staticClass:"mb-0"},[_vm._v("Los auditores están más preocupados por asegurarse de que las cosas se hagan bien que por saber quién recibe el crédito, por detectar brechas y hacer cambios. La fase de alcance es un buen momento para revelar las lagunas en la auto identificación. Algunos auditores prefieren que esto suceda durante las negociaciones del compromiso, así que es importante asegurarse de verificar las expectativas.")])])])],1),_vm._m(74)]),_c('div',{staticClass:"row justify-content-center align-items-center mb-5"},[_vm._m(75),_c('div',{staticClass:"col-lg-8",attrs:{"data-aos":"fade-left"}},[_c('LineaTiempoD',{staticClass:"color-primario"},[_c('div',{staticClass:"row",attrs:{"numero":"d","titulo":"Trabajo de campo y pruebas de control"}},[_c('p',[_vm._v("Aquí es donde se llevará a cabo la mayor parte del trabajo pesado. El auditor primero pedirá que se proporcione información sobre los procesos y procedimientos. Los auditores entrevistarán a los administradores del sistema para ver cómo llevan a cabo sus tareas diarias y luego verificarán la documentación de respaldo.")]),_c('p',{staticClass:"mb-0"},[_vm._v("Los procedimientos operativos escritos son siempre los mejores. Incluso si se hace lo correcto, si no está documentado, puede esperar que el problema se note durante la auditoría final. Sin documentación escrita, no hay garantía de que el proceso se repite de la misma manera cada vez. Si no se documentan, las transiciones o los desastres de los empleados pueden impedir que los administradores habituales realicen sus funciones y provocar cambios en los procesos.")])]),_c('div',{staticClass:"row",attrs:{"numero":"e","titulo":"Pruebas de control"}},[_c('p',[_vm._v("Una vez que el auditor ha identificado todos los controles, verifica su eficacia. Antes de examinar el sistema, los auditores tratan de determinar si los controles cumplen con los objetivos estratégicos. Revisarán el control y asumirán que se ha implementado correcta y consistentemente. También examinarán los aspectos técnicos del control y los sistemas que se supone que debe proteger. Si existe un riesgo significativo de violaciones de la política, incluso si se implementan los controles, es posible que no se consideren efectivos y deben revisarse. A veces, esto simplemente significa modificar la documentación escrita del control para reflejar mejor su diseño. A veces tendrá que borrar los comandos y empezar de nuevo.")]),_c('div',{staticClass:"BG02 p-4 mb-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("Una vez que los auditores han evaluado la efectividad de los controles, los revisan. Aquí es donde se ve que algo está funcionando bien. Aunque los controles pueden considerarse efectivos, es posible que no lo sean en la implementación real. Es posible que el procedimiento se haya realizado incorrectamente o no se haya seguido.")])])]),_c('div',{staticClass:"row",attrs:{"numero":"f","titulo":"Informes"}},[_c('p',[_vm._v("Los informes suelen tener dos partes principales:")]),_c('div',{staticClass:"BG02 p-4 mb-4"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-2 col-4"},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img95.svg"),"alt":""}})]),_c('div',{staticClass:"col-lg-10"},[_c('div',{staticClass:"h5"},[_vm._v("Sección 1: declaración de opinión")]),_c('p',{staticClass:"mb-0"},[_vm._v("El informe del auditor comienza con una declaración de su opinión sobre la eficacia y eficiencia generales de los controles de la organización. Esto debe ser sucinto si todo va bien y no hay problemas importantes que informar.")])])])]),_c('div',{staticClass:"BG02 p-4 mb-4"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{stati